.userItem {
    border: 1px solid rgb(45, 80, 165);
    margin-bottom: 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    color: #000;
}
.avatar {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.avatar img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-bottom: 5px;
    border: 1px solid rgb(45, 80, 165);
}
button {
    &:disabled{
        background: rgb(128, 128, 128);
        color: #000;
    }
}
.mainInfo {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    font-size: 14px;
    & span{
        font-weight: 700;
        font-size: 22px;
    }
}
